  margin: 0 auto 6.25em;
  line-height: 2;
}

.main-heading h2:before, .main-heading h2:after {
  left: 50%;
  transform: translateX(-50%);
}

.main-heading h2:before {
  content: "";
  z-index: 2;
  background-color: #fff;
  border: 2px solid #000;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: -32.5px;
}

.main-heading h2:after {
  content: "";
  background-color: #000;
  width: 150px;
  height: 2px;
  position: absolute;
  bottom: -25px;
}

header {
